import { Component, OnChanges, Input, OnInit, SimpleChanges, OnDestroy } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { FetchWorksService, Project, CDN } from '../fetch-works.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.scss']
})
export class ProjectComponent implements OnInit, OnChanges, OnDestroy {
  public cdn_url:string = CDN;

  public project:Project;
  public images:string[] = [];
  public videos:string[] = [];

  public subscription:Subscription;

  constructor(
    private router: Router,
    private fetchWorksService: FetchWorksService
  ) {
    this.subscription = this.router.events.subscribe(event => {
      if(event instanceof NavigationEnd) {
        this.fetchWorksService.getProject(this.router.url.split("/project/").pop())
          .subscribe((data:Project) => {
            this.project = data;

            if(this.project.images)
              this.images = this.project.images.split(',');

            if(this.project.videos)
              this.videos = this.project.videos.split(',');
          })
      }
    })
  }

  ngOnInit(): void {

  }

  ngOnChanges(changes: SimpleChanges) {

  }

  ngOnDestroy() {
    if(this.subscription)
      this.subscription.unsubscribe();
  }

}
