import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

const production:boolean = false;

@Injectable({
  providedIn: 'root'
})
export class FetchWorksService {
  private hostUrl:string = 'api.maliktillman.com';

  constructor(private http: HttpClient) { }

  getWorksList = () => {
    return this.http.get<MetaProject[]>(`https://${ this.hostUrl }/work-list`);
  }

  getProject = ( id ) => {
    return this.http.get(`https://${ this.hostUrl }/works/${ id }`);
  }

  getWorksListByType = ( type:string[] ) => {
    return this.http.get<MetaProject[]>(`https://${ this.hostUrl }/work-list/${ type.join(',') }`);
  }

  getWorksListByIds = ( ids:number[] ) => {
    // Used comma delimiter instead of semi-colon because of ad-blockers
    return this.http.get<MetaProject[]>(`https://${ this.hostUrl }/work-list_by-ids/${ ids.join(',') }`);
  }
}

export interface MetaProject {
  id: number,
  hidden: boolean,
  name: string,
  category: string,
  tags: string,
  thumbnail: string,
  github: string,
  behance: string,
  website: string
}

export interface Project {
  id: number,
  hidden: boolean,
  name: string,
  category: string,
  tags: string,
  description: string,
  thumbnail: string,
  images: string,
  videos: string,
  github: string,
  behance: string,
  website: string
}

export const CDN = production?
  'cdn.maliktillman.com/file/maliktillman-media-store':
  'maliktillman-media-store.s3.us-west-002.backblazeb2.com';
