import {Component, Input, OnDestroy, OnInit, OnChanges, SimpleChanges} from '@angular/core';
import { FetchWorksService, MetaProject, CDN } from '../../fetch-works.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'works-list',
  templateUrl: './works-list.component.html',
  styleUrls: ['./works-list.component.scss']
})
export class WorksListComponent implements OnInit, OnChanges, OnDestroy {
  public cdn_url:string = CDN;

  @Input() private types:string[];
  public worksList:MetaProject[] = [];

  public subscription:Subscription;

  constructor(
    private fetchWorksService: FetchWorksService
  ) {}

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges) {
    if(this.types)
      this.subscription = this.fetchWorksService.getWorksListByType(this.types).subscribe(data => this.worksList = data);

    else
      this.subscription = this.fetchWorksService.getWorksList().subscribe(data => this.worksList = data);
  }

  ngOnDestroy() {
    if(this.subscription)
      this.subscription.unsubscribe();
  }

  externalLink($event: MouseEvent) {
    $event.stopPropagation();
  }
}
